import './Header.css'
import React from 'react';
import { Link } from 'react-router-dom'
import logo from '../../media/logo.png';

const Header = () => {
    return(
        <header className="ui stackable menu header-component">
                <Link to="/" className="header" id="logo">
                    <img className="page-images" src={logo} alt="gsr-logo" />
                    <div className="content header-title-container">
                        <div className="header-header"><p style={{fontWeight: '800'}}>GUSTAVO RIOS</p></div>
                        <div className="header-subheader"><p style={{fontWeight: '50', color: '#696969'}}>{"{ frontEnd webDeveloper }"}</p></div>
                    </div>
                </Link>
                <div className="right menu">
                    <Link to="/" className="item item-header">Portfolio</Link>
                    <Link to="/about" className="item item-header">About me</Link>
                    <Link to="/resume" className="item item-header">Resume</Link>
                </div>
        </header>
    );
}

export default Header;